import user from "../../model/user";

import headerBack from "../../components/headerBack";

import RegExpUtil from "../../util/RegExpUtil"

import yzmView from "../../components/yzm"

export default {
  components: {
 
    headerBack,
    yzmView
  },
  data() {
    return {
      title: this.$t("register.title"), //
      codeText: "",
      telephone: "",
      password: "",
      registerCode: null,
      newTelephone: "",
      username: "",
      newPassword: "",
      code: "",
    };
  },
  methods: {
   
    //
    check() {

      if (!RegExpUtil.isMobile(this.telephone)) {
        this.$vux.toast.text("");
        return;
      }
      if (this.username == "") {
        this.$vux.toast.text("");
        return;
      }
      if (!RegExpUtil.isPassword(this.password)) {
        this.$vux.toast.text("");
        return;
      }
      this.$vux.loading.show({
        text: "Loading"
      });
      // 
      user
        .register(this.telephone, this.username, this.password, this.code)
        .then(() => {
          this.$vux.loading.hide();
          this.$vux.toast.show({
            text: ""
          });
          // 
          this.$router.push("/login");
        });
    },
    //
    getCode() {
        if (!RegExpUtil.isMobile(this.telephone)) {
        this.$vux.toast.text("");
        return;
      }
      this.$vux.toast.text("");
      user.getVerificationCode(this.telephone).then(() => {
        console.log("");
      });
      return true;
    }
  },
  mounted() {},
  created() {}
};



